	module.exports = {"data":{"markdownRemark":{"html":"<h2>Pirate Metrics</h2>\n<p>Dave McClure identified five high-level metric categories critical to a startup’s success:\nAcquisition, Activation, Retention, Revenue, Referral. </p>\n<p>He coined the term “Pirate Metrics” from the acronym for these five metric categories (AARRR).</p>\n<p>In their book Lean Analytics, Croll and Yoskovitz interpret these metrics visually as a funnel:\n<img src=\"https://github.com/yunChigewan/storage/blob/master/figure_5_1.jpg?raw=true\" alt=\"Lean Analytics Figure 5.1\">\nLean Analytics, 2013 </p>\n<p>And with more pointed explanations as a table:\n<img src=\"https://github.com/yunChigewan/storage/blob/master/table_5_1.jpg?raw=true\" alt=\"Lean Analytics Table 5.1\">\nLean Analytics, 2013</p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<ul>\n<li>Original Pirate Metrics Slideshow (McClure) <a href=\"https://www.slideshare.net/dmc500hats/startup-metrics-for-pirates-long-version\">https://www.slideshare.net/dmc500hats/startup-metrics-for-pirates-long-version</a></li>\n<li>Lean Analytics Book Website <a href=\"http://leananalyticsbook.com/\">http://leananalyticsbook.com/</a></li>\n<li>Notion has a really good guide on Pirate Metrics (including Team measures) on their <a href=\"http://usenotion.com/aarrrt/\">website</a></li>\n</ul>","fields":{"slug":"/agile/pirate-metrics/"},"frontmatter":{"title":"Pirate Metrics"}}},"pathContext":{"slug":"/agile/pirate-metrics/"}}

